import Model from '../model'

const room = {
  roomId: 0,
  roomTitle: '',
  rentType: 0, // 1. 2.
  lng: '',
  lat: '',
  roomFace: {
    type: Model.STRING,
    default: '1'
  },
  cityId: 0,
  community: '',
  districtId: '',
  road: '',
  nong: '',
  roadum: '',
  building: '',
  floorNum: '',
  roomNum: '',
  checkinTime: '',
  roomPrice: '',
  payType: '', // /1/3
  area: '', // 
  bookingPrice: '', // 
  agentType: {
    type: Model.STRING,
    default: '1'
  }, // 
  picDetails: {
    type: Model.ARRAY,
    default: []
  },
  agentPercent: '',
  agentPrice: '',
  agentId: '',
  roomMemo: '',
  roomNumId: '',
  rentStatus: {
    type: Model.STRING,
    default: '1'
  }, // 1. 2. 3.
  editType: {
    type: Model.NUMBER,
    default: 1
  }// 1. 2.,
}

export default new Model(room);


