<style lang="less" scoped>
.room-management-vue {
  .weui_panel {
    margin-top: 0;
  }
  .room-item {
    padding: 0 10px;
    font-size: 14px;
    .item-agent, .item-customer {
      padding: 8px 0;
      line-height: 28px;
      a {
        padding: 0 30px;
      }
    }
  }
  .weui-form-preview {
    position: relative;
    background-color: #FFFFFF;
    &:before {
      content: " ";
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      height: 1px;
      border-top: 1px solid #D9D9D9;
      color: #D9D9D9;
      -webkit-transform-origin: 0 0;
      transform-origin: 0 0;
      -webkit-transform: scaleY(0.5);
      transform: scaleY(0.5);
    }
    &:after {
      content: " ";
      position: absolute;
      left: 0;
      bottom: 0;
      right: 0;
      height: 1px;
      border-bottom: 1px solid #D9D9D9;
      color: #D9D9D9;
      -webkit-transform-origin: 0 100%;
      transform-origin: 0 100%;
      -webkit-transform: scaleY(0.5);
      transform: scaleY(0.5);
      left: 15px;
    }
    .weui-form-preview__hd {
      position: relative;
      padding: 10px 15px;
      text-align: right;
      line-height: 2.5em;
      &:after {
        content: " ";
        position: absolute;
        left: 0;
        bottom: 0;
        right: 0;
        height: 1px;
        border-bottom: 1px solid #D9D9D9;
        color: #D9D9D9;
        -webkit-transform-origin: 0 100%;
        transform-origin: 0 100%;
        -webkit-transform: scaleY(0.5);
        transform: scaleY(0.5);
        left: 15px;
      }
      .weui-form-preview__value {
        font-style: normal;
        font-size: 1.3em;
      }
    }
    .weui-form-preview__bd {
      padding: 10px 15px;
      font-size: .9em;
      text-align: right;
      color: #999999;
      line-height: 2;
    }
    .weui-form-preview__item {
      overflow: hidden;
      .weui-form-preview__label {
        float: left;
        margin-right: 1em;
        min-width: 4em;
        color: #999999;
        text-align: justify;
        text-align-last: justify;
      }
    }
    .weui-form-preview__value {
      display: block;
      overflow: hidden;
      word-break: normal;
      word-wrap: break-word;
    }
  }
}
</style>

